"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useEffect, useState } from "react";
import { useTheme } from "next-themes";
import {
  LogOut,
  LayoutDashboard,
  Users,
  ClipboardList,
  Gift,
  FileText,
  ListChecks,
  Moon,
  Sun,
} from "lucide-react";

export default function ShellClient({
  user,
  children,
}: {
  user: any;
  children: React.ReactNode;
}) {
  const pathname = usePathname();
  const appName = process.env.NEXT_PUBLIC_APP_NAME || "SMJJ";
  const [showLogoutConfirm, setShowLogoutConfirm] = useState(false);
  const handleLogoutClick = () => setShowLogoutConfirm(true);
  const cancelLogout = () => setShowLogoutConfirm(false);
  const confirmLogout = () => {
    setShowLogoutConfirm(false);
    window.location.href = "/api/logout";
  };

  // dYO- untuk tema gelap / terang
  const { theme, setTheme } = useTheme();
  const [mounted, setMounted] = useState(false);
  useEffect(() => setMounted(true), []);
  const isDark = theme === "dark";

  const menuItems = [
    { name: "Dashboard", href: "/dashboard", icon: <LayoutDashboard size={20} /> },
    { name: "Pelanggan", href: "/pelanggan", icon: <Users size={20} /> },
    { name: "Menu", href: "/menu", icon: <ListChecks size={20} /> },
    { name: "Riwayat", href: "/riwayat", icon: <ClipboardList size={20} /> },
    { name: "Reward", href: "/reward", icon: <Gift size={20} /> },
  ];

  if (user?.role === "OWNER") {
    menuItems.push({ name: "Laporan", href: "/laporan", icon: <FileText size={20} /> });
  }

  return (
    <div className="relative min-h-screen overflow-hidden bg-gradient-to-br from-rose-50 via-white to-slate-100 text-gray-900 transition-colors duration-300 dark:from-[#050505] dark:via-[#0d0d0d] dark:to-[#151515] dark:text-gray-100">
      <div className="pointer-events-none absolute inset-0">
        <div className="absolute -top-24 -left-16 h-72 w-72 rounded-full bg-rose-400/25 blur-[140px] dark:bg-rose-500/10" />
        <div className="absolute bottom-[-20%] right-[-10%] h-96 w-96 rounded-full bg-sky-300/25 blur-[160px] dark:bg-sky-500/10" />
      </div>

      <div className="relative z-10 flex min-h-screen w-full flex-col gap-6 px-4 py-6 lg:flex-row lg:px-8 lg:py-10">
        {/* Sidebar modern */}
        <aside className="flex w-full flex-shrink-0 flex-col justify-between rounded-3xl border border-white/60 bg-white/80 px-6 py-6 shadow-2xl backdrop-blur-2xl transition-all duration-300 dark:border-white/10 dark:bg-white/5 lg:w-72">
          {/* Profil user */}
          <div className="space-y-4 text-center">
            <p className="text-xs font-semibold uppercase tracking-[0.4em] text-rose-500/80 dark:text-rose-200/80">
              {appName}
            </p>
            <div className="mx-auto flex h-20 w-20 items-center justify-center rounded-2xl bg-gradient-to-br from-rose-500 to-orange-400 text-white shadow-lg shadow-rose-200/60 dark:shadow-rose-950/30">
              <Users size={34} />
            </div>
            <div>
              <p className="text-sm text-gray-500 dark:text-gray-300/80">Selamat datang</p>
              <h2 className="text-xl font-semibold text-gray-900 dark:text-white">{user?.name || "Pengguna"}</h2>
              <span className="mt-2 inline-flex items-center rounded-full bg-rose-50 px-3 py-1 text-xs font-medium text-rose-600 dark:bg-white/10 dark:text-rose-200">
                {user?.role || "Pegawai"}
              </span>
            </div>
          </div>

          {/* Navigasi */}
          <nav className="mt-8 flex flex-1 flex-col space-y-1 text-sm">
            {menuItems.map((item) => {
              const isActive = pathname === item.href;
              return (
                <Link
                  key={item.name}
                  href={item.href}
                  className={`group flex items-center gap-3 rounded-2xl px-4 py-2.5 transition-all duration-200 ${
                    isActive
                      ? "bg-rose-500/15 text-rose-600 shadow-inner shadow-rose-200/50 dark:bg-white/10 dark:text-white"
                      : "text-gray-600 hover:bg-gray-100/80 hover:text-gray-900 dark:text-gray-300 dark:hover:bg-white/5 dark:hover:text-white"
                  }`}
                >
                  <span
                    className={`flex h-9 w-9 items-center justify-center rounded-xl border text-sm ${
                      isActive
                        ? "border-rose-500 bg-white text-rose-600 dark:border-white/30 dark:bg-white/10 dark:text-white"
                        : "border-gray-200 text-gray-500 dark:border-white/10 dark:text-gray-300"
                    }`}
                  >
                    {item.icon}
                  </span>
                  <span className="flex-1 font-medium">{item.name}</span>
                  <span className="text-lg text-gray-300 opacity-0 transition-opacity duration-200 group-hover:opacity-100 dark:text-gray-500">
                    &rsaquo;
                  </span>
                </Link>
              );
            })}
          </nav>

          {/* Bagian bawah: Toggle & Logout */}
          <div className="mt-6 space-y-3 rounded-2xl border border-white/60 bg-white/90 p-4 shadow-inner shadow-gray-200/60 backdrop-blur-xl dark:border-white/10 dark:bg-white/5 dark:shadow-black/40">
            {/* Tombol toggle tema */}
            {mounted && (
              <button
                onClick={() => setTheme(isDark ? "light" : "dark")}
                className="flex items-center justify-between gap-3 rounded-2xl bg-gray-900/5 px-4 py-3 text-sm font-medium text-gray-700 transition hover:bg-gray-900/10 dark:bg-white/10 dark:text-gray-100 dark:hover:bg-white/20"
              >
                <span className="flex items-center gap-2">
                  {isDark ? <Sun size={18} /> : <Moon size={18} />}
                  {isDark ? "Mode Terang" : "Mode Gelap"}
                </span>
                <span className="text-xs uppercase tracking-widest text-gray-400">Toggle</span>
              </button>
            )}

            {/* Tombol logout */}
            <button
              type="button"
              onClick={handleLogoutClick}
              className="flex w-full items-center justify-between gap-3 rounded-2xl bg-gradient-to-r from-rose-500 to-orange-500 px-4 py-3 text-sm font-semibold text-white shadow-lg shadow-rose-200/70 transition hover:shadow-xl dark:shadow-rose-900/40"
            >
              <span className="flex items-center gap-2">
                <LogOut size={18} />
                Logout
              </span>
              <span className="text-xs uppercase tracking-widest">Keluar</span>
            </button>
          </div>
        </aside>

        {/* Konten utama */}
        <main className="flex-1 rounded-[32px] border border-white/70 bg-white/80 p-4 shadow-2xl shadow-gray-200/70 backdrop-blur-2xl transition-colors duration-300 dark:border-white/10 dark:bg-black/40 dark:shadow-black/50 sm:p-8">
          <div className="min-h-full">{children}</div>
        </main>
      </div>

      {showLogoutConfirm && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 p-4 backdrop-blur-sm">
          <div
            role="alertdialog"
            aria-modal="true"
            aria-labelledby="logout-title"
            className="w-full max-w-md rounded-3xl border border-white/70 bg-white/95 p-6 text-gray-900 shadow-2xl dark:border-white/10 dark:bg-slate-900 dark:text-gray-100"
          >
            <div className="flex items-center gap-3">
              <div className="rounded-2xl bg-rose-100 p-3 text-rose-600 dark:bg-rose-500/20 dark:text-rose-200">
                <LogOut size={22} />
              </div>
              <div>
                <p className="text-xs font-semibold uppercase tracking-[0.3em] text-rose-500/70 dark:text-rose-200/70">
                  Konfirmasi
                </p>
                <h2 id="logout-title" className="text-lg font-bold">
                  Keluar dari sesi?
                </h2>
              </div>
            </div>
            <p className="mt-4 text-sm text-gray-600 dark:text-gray-300">
              Anda akan keluar dari aplikasi ini dan harus login kembali untuk melanjutkan.
            </p>
            <div className="mt-6 flex justify-end gap-3">
              <button
                type="button"
                onClick={cancelLogout}
                className="rounded-2xl border border-gray-200 px-4 py-2 text-sm font-semibold text-gray-600 transition hover:bg-gray-50 dark:border-white/10 dark:text-gray-300 dark:hover:bg-white/10"
              >
                Batal
              </button>
              <button
                type="button"
                onClick={confirmLogout}
                className="rounded-2xl bg-gradient-to-r from-rose-500 to-orange-500 px-4 py-2 text-sm font-semibold text-white shadow-lg shadow-rose-200/70 transition hover:shadow-xl dark:shadow-rose-900/40"
              >
                Logout
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
